.header {
  margin: 0;
  text-align: right;
  padding: 0 10%;
}

nav ul {
  display: inline-flex;
}

nav li {
  width: 60px;
  padding-right: 40px;
}

nav a:link {
  color: var(--main-darkgreen);
}

/* Language Picker */
.languagePicker {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border: 1px solid rgba(0,0,0,.15);
  border-radius: 10px;
}

button span{
  font-size: 0.75rem;
  margin: 0;
  padding: 0;
}