.bio {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 30px;
}

img {
  border-radius: 50%;
  width: 160px;
  height: 160px;
  margin: 5px 50px 10px;
}

h1 {
  color: var(--main-darkgreen);
  font-size: 2rem;
  line-height: 1.2;
  letter-spacing: 0.075em;
  margin-top: 0;
  margin-bottom: 15px;
}

h2 {
  color: var(--main-beige);
  font-size: 1.25rem;
  line-height: 1.2;
}

p {
  font-weight: 400;
  max-width: 60ch;
}